/*-------- Page-progress --------*/

.page-progress {

    &__bar {
        display: flex;
        justify-content: center;
        position: fixed;
        left: 0;
        top: 0;
        width: 4px;
        height: 100%;
        background-color: #3e3e3e;
        opacity: 0.35;
        z-index: 11;
    }

    &__track {
        width: 6px;
        height: 0;
        background-color: #2998e5;
        position: absolute;
        top: 0;
        left: 0;
        transition: height 0.1s ease-out;
    }

    &__image-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 80px;
        height: 80px;
        position: fixed;
        right: 30px;
        bottom: 30px;
        cursor: pointer;
        z-index: 11;
    }

    &__image-wrapper:hover &__count {
        color: #2998e5;
    }

    &__image {
        width: 100%;
        height: 100%;
        transition: transform 0.1s ease-out;
    }

    &__count {
        font-family: $font-pf;
        font-size: 30px;
        color: #000000;
        position: absolute;
        transition: color 0.2s ease-out;
    }
}