/*-------- Cursor --------*/

.cursor { 
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    position: absolute;
    transform: translate(-50%, -50%);
    z-index: 999999999;
    pointer-events: none;

    &__wrapper {
        display: flex;
        justify-content: center;
        align-items: center;

        width: 100%;
        height: 100%;
        
        background: #4ebeff;
        
        border-radius: 50%; 
        opacity: 0.8;
        transition: background 0.5s, transform 0.3s ease-out, opacity 0.2s ease-out;
    }

    &__arrow {
        width: 5px;
        height: 5px;
        position: absolute;
        border: 2px solid #000000;
        border-left: none;
        border-top: none;  

        transform: top 0.4s ease-out, transform 0.4s ease-out, left 0.4s ease-out, right 0.4s ease-out, bottom 0.4s ease-out, opacity 0.3s ease-out;
    }

    &--scroll &__arrow--1 {
        top: 10px;
        transform: rotateZ(-135deg);
    }

    &--scroll &__arrow--2 {
        bottom: 10px;
        transform: rotateZ(45deg); 
    }

    &--hover &__wrapper, &--grab &__wrapper {
        transform: scale(1.4);
    }

    &--hover &__arrow {
        opacity: 0;
    }

    &--grab &__arrow--1 {
        top: unset;
        left: 10px;
        transform: rotateZ(135deg); 
    }

    &--grab &__arrow--2 {
        bottom: unset;
        right: 10px;
        transform: rotateZ(-45deg); 
    }

    &--pointer &__wrapper {
        opacity: 0;
    }
}

.cursor {
    
    &.active &__wrapper {
        background: #2a8ac2;
    }
}

.cursor {

    &.active &__wrapper:after {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.cursor {

    &.cursor-pointer &__wrapper:after {
        content: "!";
    }
}

.cursor {

    &.cursor-right &__wrapper:after {
        content: ">";
    }
}

.cursor {

    &.cursor-left &__wrapper:after {
        content: "<";
    }
}