/*-------- Banner --------*/

.banner { 
    position: relative;
    overflow: hidden;
    z-index: 3;
}

.banner {

    &--main:before {
        content: '';
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
    }

    &--main &__wrapper {
        padding-top: 129px;
        padding-bottom: 202px;
        position: relative;
        z-index: 2;
    }

    &--main &__title {
        margin-top: 85px;

        font-family: $font-pf;
        font-size: 124px;
        color: #ffffff;
    }

    &--main &__title-text-mask {
        overflow: hidden;
    }

    &--main &__title-text--blue {
        color: $blue;
    }

    &--main &__text {
        display: flex;
        flex-wrap: wrap;
        margin-top: 34px;

        max-width: 671px;
        font-family: $font-r;
        font-size: 30px;
        color: #ffffff;
        line-height: 1.4;

        overflow: hidden;
        white-space: pre;
    }

    &--main &__buttons {
        display: flex;
        margin-top: 80px;
    }

    &--main &__button:not(:first-child) {
        margin-left: 18px;
    }
}

.banner {

    &--v2 {
        height: 733px;
        position: relative;
        overflow: hidden;
    }

    &--v2 &__wrapper {
        height: 100%;
        width: 100%;

        position: absolute;
        left: 0;
        top: 0;
    }

    &--v2 &__image-wrapper {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        overflow: hidden;
    }

    &--v2 &__image {
        width: 100%;
        height: 100%;

        position: absolute;
        left: 0;
        top: 0;
        z-index: 0;

        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    &--v2 &__image-wrapper:before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: #000000;
    }

    &--v2 &__wrapper-inner {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;

        z-index: 1;
        position: relative;
    }

    &--v2 &__title {
        font-family: $font-pf;
        font-size: 64px;
        color: #ffffff;
        text-align: center;
    }

    &--v2 &__arrow {
        width: 16px;
        height: 16px;

        position: absolute;
        bottom: 106px;

        border: 2px solid #ffffff;
        border-left: none;
        border-top: none;
        transform: rotateZ(45deg);
        cursor: pointer;
    }
}

.banner {

    &--case-study &__title {
        font-size: 58px;
    }
}

.banner {

    &--result {
        height: 675px;
    }

    &--result &__title {
        font-family: $font-pf;
        font-size: 58px;
    }

    &--result &__image-wrapper {
        height: 120%;
        top: -20%;
    }
}

.banner {

    &--next {
        display: block;
        height: 625px;     
        margin-bottom: -70px;
        position: relative;
    }

    &--next:hover:before {
        background-color: rgba(0, 0, 0, 0.8);
    }

    &--next:hover &__image {
        transform: scale(1.05);
    }

    &--next:hover &__title--big {
        opacity: 0.4;
    }

    &--next:hover &__title-icon {
        margin-left: 20px;
    }

    &--next:before {
        content: '';
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.9);
        position: absolute;
        left: 0;
        top: 0;
        z-index: 0;

        transition: background-color 0.2s cubic-bezier(0.2, 0, 0.175, 1);
    }

    &--next &__image {
        width: 100%;
        height: 100%;

        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;

        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;

        transition: transform 0.5s cubic-bezier(0.2, 0, 0.175, 1);
    }

    &--next &__wrapper {
        display: flex;
        justify-content: center;
        flex-direction: column;
        height: 100%;
        z-index: 2;
        position: relative;
    }

    &--next &__title {
        font-family: $font-pf;
        color: #ffffff;   
    }

    &--next &__title--big {
        font-size: 124px;
        opacity: 0.13;

        transition: opacity 0.2s cubic-bezier(0.2, 0, 0.175, 1);
    }

    &--next &__title--small {
        font-size: 58px;
        margin-top: 20px;
    }

    &--next &__title--arrow {
        display: flex;
        align-items: center;
        margin-top: 26px;
        font-family: $font-b;
        font-size: 15px;
    }

    &--next &__title-icon {
        width: 17px;
        margin-left: 15px;
        fill: #ffffff;

        transition: margin-left 0.2s cubic-bezier(0.8, 0, 0.175, 1);
    }

    &--next &__title-text {
        border-bottom: 1px solid rgba(255, 255, 255, 0.6);
    }
}

@media (max-width: 1499px) {

    .banner {

        &--main &__wrapper {
            padding-bottom: 150px;
        }

        &--main &__title {
            margin-top: 60px;
            font-size: 110px;
        }
    }
}

@media (max-width: 1399px) {

    .banner {

        &--main &__title {
            font-size: 80px;
        }

        &--main &__text {
            margin-top: 20px;
            font-size: 25px;
        }

        &--main &__buttons {
            margin-top: 60px;
        }
    }

    .banner {

        &--v2 {
            height: 600px;
        }
    }
}

@media (max-width: 1219px) {

    .banner {

        &--main &__title {
            font-size: 64px;
        }

        &--main &__text {
            font-size: 22px;
        }
    }

    .banner {

        &--result {
            height: 500px;
        }
    }

    .banner {

        &--next {
            height: 540px;
        }
    }
}

@media (max-width: 1099px) {

    .banner {

        &--main &__wrapper {
            padding-top: 110px;
        }
    }

    .banner {

        &--v2 {
            height: calc(100vh - 90px);
        }

        &--v2 &__title {
            font-size: 52px;
        }

        &--v2 &__arrow {
           bottom: 50px;
        }
    }
}

@media (max-width: 989px) {

    .banner {

        &--main {
            height: 100vh;
            display: flex;
            align-items: center;
        }

        &--main &__wrapper {
            padding-top: 90px;
        }

        &--main &__title {
            margin-top: 40px;
            font-size: 50px;
        }

        &--main &__text {
            margin-top: 15px;
            font-size: 18px;
        }

        &--main &__buttons {
            margin-top: 30px;
        }
    }

    .banner {

        &--v2 &__title {
            font-size: 45px;
        }
    }

    .banner--result {
        height: 450px;
    }

    .banner {

        &--next {
            height: 400px;
        }

        &--next &__title--big {
            font-size: 75px;
        }

        &--next &__title--small {
            font-size: 45px;
        }
    }
}

@media (max-width: 767px) {

    .banner {

        &--v2 {
            height: 300px;
        }
    }

    .banner {

        &--next &__wrapper {
            text-align: center;
            align-items: center;
        }
    }
}

@media (max-width: 619px) {

    .banner {

        &--main &__wrapper {
            padding-bottom: 100px;
        }
    }

    .banner {

        &--v2 {
            height: 250px;
        }

        &--v2 &__title {
            font-size: 30px;
        }

        &--v2 &__arrow {
            width: 10px;
            height: 10px;
        }
    }

    .banner {

        &--next {
            height: 300px;
        }

        &--next &__title--big {
            font-size: 60px;
        }
    }
}

@media (max-width: 479px) {

    .banner {

        &--main &__wrapper {
            padding-bottom: 60px;
        }

        &--main &__title {
            margin-top: 10px;
            font-size: 43px;
        }

        &--main &__text {
            font-size: 15px;
        }
    }

    .banner {

        &--result {
            height: 200px;
        }
    }

    .banner {

        &--next {
            height: 250px;
        }

        &--next &__title--big {
            font-size: 46px;
        }

        &--next &__title--small {
            margin-top: 15px;
            font-size: 31px;
        }

        &--next &__title--arrow {
            margin-top: 15px;
            font-size: 14px;
        }
    }
}